@import url('https://fonts.googleapis.com/css2?family=Rubik:ital,wght@0,300;0,400;0,500;0,700;0,800;1,500;1,600;1,700;1,800;1,900&display=swap');

*,::after,::before{
  font-family: 'Rubik', sans-serif;
  margin: 0;
  padding: 0;
  border: none;
  outline: none;
  list-style: none;
  user-select: none;

}

.ant-card-bordered {
  border: none;
}
::-webkit-scrollbar {
  width: 2px;
display: none;
}
 
::-webkit-scrollbar-track {
  box-shadow: inset 0 0 6px rgba(0, 0, 0, 0.3);
}
 
::-webkit-scrollbar-thumb {
  width: 2px;
  background-color: darkgrey;
  outline: 1px solid slategrey;
  border-radius: 5px;
}